* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background: #f2f2f2;
  color: #6d6d6d;
  width: 100vw;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  width: 70%;
  position: absolute;
  top: 15%;
}
.app {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

form{
    margin-bottom: 15px;
}
input {
    padding: 20px;
    border: 0;
    background-color: #e4e4e4;
    font-size: 16px;
    transition: all 0.3s ease-in-out;
}
input[type="text"]:focus {
    outline: 0;
    background: #d2d2d2;
}
input[type="submit"]:focus,
input[type="submit"]:hover {
    background: #e85a40;
}
input {
    outline: 0;
}
.task {
    width: 60%;
}
.type {
    width: 30%;
}
.btn {
    width: 10%;
    background: tomato;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    line-height: 18px;
    vertical-align: bottom;
}
.warning {
    color: tomato;
    margin: 20px;
}

.titles {
    width: 100%;
    display: flex;
    background: #6d6d6d;
    padding: 10px 0;
    color: #fff;
}
.noItem {
    border-bottom: solid 0.3px #ddd;
    height: 100%;
    text-align: center;
    color: #cccccc;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.num {
    width: 7%;
    padding: 0 20px;
    text-align: center;
}
.firstSection {
    width: 53%;
    padding: 0 20px;
}
.secSection {
    width: 30%;
    text-align: center;
}
.thirSection {
    width: 10%;
    text-align: center;
}
.taskNum {
    width: 7%;
    border-right: solid 0.3px #ddd;
    text-align: center;
}
.taskName {
    width: 53%;
    font-weight: 700;
    border-right: solid 0.3px #ddd;
}
.taskType {
    width: 30%;
    text-align: center;
    border-right: solid 0.3px #ddd;
}
.delIcon {
    width: 10%;
    text-align: center;
}
.taskNum,
.taskName,
.taskType,
.delIcon {
    padding: 20px;
    border-bottom: solid 0.3px #ddd;
}
.delIcon {
    color: tomato;
    font-size: 32px;
    padding: 0;
    line-height: 58px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.delIcon:hover {
    background: tomato;
    color: #fff;
}
.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 440px;
    overflow: overlay;
    background: #f9f9f9;
    border: 0.5px solid #ddd;
    padding: 0 1px 0 0;
}
.items {
    display: flex;
    justify-content: space-between;
    background: #fff;
}
::-webkit-scrollbar {
    width: 4px;
}
body::-webkit-scrollbar-track {
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb {
    background-color: #e4e4e4;
}

